import React, { useEffect, useRef, useState } from "react";
import { useHistory } from "react-router";
import styles from "./register.module.css";

const Register = ({ authService, database, uid, loadUserInformation }) => {
  const history = useHistory();
  const sexRef = useRef();
  const ageRef = useRef();
  const heightRef = useRef();
  const weightRef = useRef();
  const activityRef = useRef();
  const [loginUser, setLoginUser] = useState();
  useEffect(() => {
    authService.onAuthStateChanged((user) => {
      if (user) {
        setLoginUser({
          userid: user.uid,
        });
      } else {
        history.push("/");
      }
    });
    return () => {
      setLoginUser();
    };
  }, [authService, history]);
  const handleOnSubmit = () => {
    const sex = sexRef.current.value;
    const age = ageRef.current.value;
    const height = heightRef.current.value;
    const weight = weightRef.current.value;
    const activity = activityRef.current.value;
    if (age === "" || height === "" || weight === "") {
      alert("   .    .");
    } else {
      let activityPoint;
      if (activity === "large") {
        activityPoint = 40;
      } else if (activity === "medium") {
        activityPoint = 33;
      } else if (activity === "small") {
        activityPoint = 25;
      }
      const recommendedCalories = Math.round(
        (height - 100) * 0.9 * activityPoint
      ); //   
      const requiredInformation = {
        sex,
        age,
        height,
        weight,
        activity,
        activityPoint,
        recommendedCalories,
      };
      database.setRequiredInformation(loginUser.userid, requiredInformation);
      database.getUserData(uid).then((response) => {
        const data = {
          ...response,
          information: {
            ...requiredInformation,
          },
        };
        loadUserInformation(data);
      });
      history.push("/main");
    }
  };

  return (
    <>
      <div className={styles.section}>
        <div className={styles.textBox}>
          <p className={styles.bigText}>  </p>
          <p className={styles.bigText}>   </p>
          <p className={styles.middleText}>
                 
          </p>
        </div>
        <input
          className={styles.slide}
          type="radio"
          name="slide"
          id="slide01"
          defaultChecked
        />
        <input
          className={styles.slide}
          type="radio"
          name="slide"
          id="slide02"
        />
        <input
          className={styles.slide}
          type="radio"
          name="slide"
          id="slide03"
        />
        <input
          className={styles.slide}
          type="radio"
          name="slide"
          id="slide04"
        />
        <input
          className={styles.slide}
          type="radio"
          name="slide"
          id="slide05"
        />
        <ul className={styles.slidelist}>
          <li className={styles.li1}>
            <div className={styles.display}>
              <p className={styles.titleText}>1.  </p>
              <select ref={sexRef} className={styles.healthInput} name="sex">
                <option value="male"></option>
                <option value="female"></option>
              </select>
              <label
                htmlFor="slide02"
                className={`${styles.right} ${styles.label}`}
              >
                <i className={`fas fa-arrow-right ${styles.icon}`}></i>
                <div className={styles.labelText}></div>
              </label>
            </div>
          </li>
          <li className={styles.li2}>
            <div className={styles.display}>
              <p className={styles.titleText}>2.  </p>
              <input
                ref={ageRef}
                className={styles.healthInput}
                type="number"
                name="age"
                placeholder="age"
                required
              />
              <div className={styles.labelBox}>
                <label
                  htmlFor="slide01"
                  className={`${styles.left} ${styles.label}`}
                >
                  <i className={`fas fa-arrow-left ${styles.icon}`}></i>
                  <div className={styles.labelText}></div>
                </label>
                <label
                  htmlFor="slide03"
                  className={`${styles.right} ${styles.label}`}
                >
                  <i className={`fas fa-arrow-right ${styles.icon}`}></i>
                  <div className={styles.labelText}></div>
                </label>
              </div>
            </div>
          </li>
          <li className={styles.li3}>
            <div className={styles.display}>
              <p className={styles.titleText}>3.  </p>
              <input
                ref={heightRef}
                className={styles.healthInput}
                type="number"
                name="height"
                placeholder="height"
                required
              />
              <div className={styles.labelBox}>
                <label
                  htmlFor="slide02"
                  className={`${styles.left} ${styles.label}`}
                >
                  <i className={`fas fa-arrow-left ${styles.icon}`}></i>
                  <div className={styles.labelText}></div>
                </label>
                <label
                  htmlFor="slide04"
                  className={`${styles.right} ${styles.label}`}
                >
                  <i className={`fas fa-arrow-right ${styles.icon}`}></i>
                  <div className={styles.labelText}></div>
                </label>
              </div>
            </div>
          </li>
          <li className={styles.li4}>
            <div className={styles.display}>
              <p className={styles.titleText}>4.  </p>
              <input
                ref={weightRef}
                className={styles.healthInput}
                type="number"
                name="weight"
                placeholder="weight"
                required
              />
              <div className={styles.labelBox}>
                <label
                  htmlFor="slide03"
                  className={`${styles.left} ${styles.label}`}
                >
                  <i className={`fas fa-arrow-left ${styles.icon}`}></i>
                  <div className={styles.labelText}></div>
                </label>
                <label
                  htmlFor="slide05"
                  className={`${styles.right} ${styles.label}`}
                >
                  <i className={`fas fa-arrow-right ${styles.icon}`}></i>
                  <div className={styles.labelText}></div>
                </label>
              </div>
            </div>
          </li>
          <li className={styles.li5}>
            <div className={styles.display}>
              <p className={styles.titleText}>5.  </p>
              <select
                ref={activityRef}
                className={styles.healthInput}
                name="activity"
              >
                <option value="large"> </option>
                <option value="medium">  </option>
                <option value="small"> </option>
              </select>
              <div className={styles.descriptionBox}>
                <p className={styles.description}>
                  <span> </span> :     
                   
                </p>
                <p className={styles.description}>
                  <span>  </span> :   
                    
                </p>
                <p className={styles.description}>
                  <span> </span> :     
                      
                </p>
              </div>
              <div className={styles.lastLabelBox}>
                <label
                  htmlFor="slide04"
                  className={`${styles.left} ${styles.label}`}
                >
                  <i className={`fas fa-arrow-left ${styles.icon}`}></i>
                  <div className={styles.labelText}></div>
                </label>
                <div onClick={handleOnSubmit} className={styles.completeBtn}>
                  <i
                    className={`fas fa-check-circle ${styles.completeIcon}`}
                  ></i>
                  <p className={styles.completeText}></p>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </>
  );
};

export default Register;
