import React from "react";
import { useRef } from "react";
import styles from "./find.module.css";

const Find = ({ authService, history }) => {
  const emailRef = useRef();
  const sendSuccess = () => {
    alert("    !");
    history.push({
      pathname: "/find/sent",
      state: {
        email: emailRef.current.value,
      },
    });
  };
  const sendFail = (error) => {
    if (error.code === "auth/user-not-found") {
      alert("  .");
    } else {
      alert(error.message);
    }
  };
  const handleOnSubmit = (event) => {
    event.preventDefault();
    const email = emailRef.current.value;
    authService.sendPasswordResetEmail(email, sendSuccess, sendFail);
  };
  return (
    <div className={styles.container}>
      <i className={`fas fa-unlock ${styles.icon_lock}`}></i>
      <h1 className={styles.title}> ?</h1>
      <form className={styles.form} onSubmit={handleOnSubmit}>
        <div className={styles.text}>
          <p className={styles.text_element}>
               .
          </p>
          <p className={styles.text_element}>
               .
          </p>
        </div>
        <input
          type="email"
          required={true}
          className={styles.input}
          placeholder=""
          ref={emailRef}
        />
        <button className={styles.button_submit}>  </button>
      </form>
    </div>
  );
};

export default Find;
